const useLang = () => {
  const lang = useStore(state => state.lang);
  const setLang = useStore(state => state.setLang);

  const toggleLang = () => {
    if (lang === 'en') setLang('fr');
    else setLang('en');
  };

  const tr = (text: string) => {
    if (lang == 'en') {
      return text;
    } else {
      return dictionary[text];
    }
  };

  return { toggleLang, tr };
};

export default useLang;
